import React, { useEffect, useContext } from 'react';
import './Home.css';
import { Link } from 'react-router-dom';
import { motion, useAnimation } from 'framer-motion';

import logo from '../img/logoSlogan.png';
import chooseMealImg from '../img/choose-meal.png';
import chefWomanImg from '../img/chef-woman.png';
import hungryAndHourglassImg from '../img/hungry-and-hourglass.png';
import tiaDriveImg from '../img/tia-drive02.png';
import womanRelaxImg from '../img/woman-relax.jpg';

// store
import { store } from '../store';

// constants
const plans = {
  plan4: { amountMeals: 4, pricePerMeal: 14.90 },
  plan6: { amountMeals: 6, pricePerMeal: 13.90 },
  plan9: { amountMeals: 9, pricePerMeal: 13.90 },
  plan12: { amountMeals: 12, pricePerMeal: 12.90 },
  // planCustom: { amountMeals: 12, pricePerMeal: 12.90}
}

// animation
const planPriceVariants = {
  initial: { scale: 1, fontWeight: "normal" },
  change: { scale: 1.1, fontWeight: "bolder" },
}

function Home() {
  // GLOBAL STATE
  const { plan, planDispatchers } = useContext(store);
  const { setPlan } = planDispatchers;


  // animatino
  const planPriceVariantsControl = useAnimation();

  useEffect(() => {
    const changePlanPrice = async () => {
      await planPriceVariantsControl.start("change");
      await planPriceVariantsControl.start("initial");
    }
    changePlanPrice();
  }, [plan]);




  return (
    <motion.div
        initial={{ opacity: 0, y: -100 }}
        animate={{ opacity: 1, y: 0 }}            
    >
    <div className="Home">
      <header className="header">
        <a href="#"><img src={logo} alt="logo" /></a>
        <h2>Você assina Netflix, mas nunca pensou em assinar refeições??</h2>
        <p>Essa é a melhor forma de garantir e ESTOCAR as refeições da sua família durante a <em>QUARENTENA</em>, sem tirar de quem precisa!</p>
        <div></div>
        <div></div>
      </header>

      <main>
        <article className="meal-style">
          <div className="meal-style-text">
            <h2>COMIDA DE VERDAE CONGELADA: <span>TRADICIONAIS</span> &amp; <span>FIT</span></h2>
            <p>em breve teremos refeições Low Carb e Funcionais.</p>
          </div>
          <div className="meal-style-bg"></div>
        </article>

        <article className="how-it-works">
          <h1>Como funciona?</h1>

          <div className="how-it-works-item">
            {/* <span className="emoji" role="img" >🍽</span> */}
            <img src={chooseMealImg} alt="escolha suas refeições favoritas" />
            <h3>Escolha Suas Refeições Favoritas!</h3>
            <p>diversas refeições congeladas e muitas ainda por vir. Tanto Linha Tradicional quanto pratos Fit</p>
          </div>

          <div className="how-it-works-item">
            {/* <span className="emoji" role="img" >🛵👩‍🍳</span> */}
            <img src={chefWomanImg} alt="cozinheiro" />
            <h3>A Gente Cozinha e Entrega</h3>
            <p>Cozinhamos com todo o carinho e capricho que tu conhece e te entregamos toda semana.</p>
          </div>

          <div className="how-it-works-item">
            {/* <span className="emoji" role="img" >🛵👩‍🍳</span> */}
            <img src={tiaDriveImg} alt="cozinheiro" />
            <h3>Tia Drive</h3>
            <p>O drive-thru da Tia Leila. É só passar e retirar suas refeições <br/><b>SEM DESCER DO CARRO!</b><br/>se preferir entregamos na porta da sua casa.</p>
          </div>

          <div className="how-it-works-item">
            {/* <span className="emoji" role="img" >⏳😋</span> */}
            <img src={hungryAndHourglassImg} alt="cozinheiro" />
            <h3>Apitou, ta pronto!</h3>
            <p>Pronto pra comer de 3 a 6min</p>
          </div>

          <div className="how-it-works-item">
            {/* <span className="emoji" role="img" >🤩🥰</span> */}
            <img src={womanRelaxImg} alt="cozinheiro" />
            <h3>Relax!</h3>
            <ul>
              <li><p><b>#</b> Sem ir as compras, sem cozinhar, sem precisar limpar</p></li>
              <li><p><b>#</b> pule uma semana</p></li>
              <li><p><b>#</b> mude a quantidade de refeições</p></li>
              <li><p><b>#</b> cancele a qualquer momento</p></li>
              <li><p><b>#</b> escolha outras refeições. Você é quem manda!</p></li>
            </ul>
          </div>
        </article>

        <article className="plans">
          <h1>PLANOS</h1>

          <div className="plan-info">
            <div className="plan-items">
              <p>refeições por semana:</p>
              <ul>
                <li onClick={() => { setPlan(plans.plan4) }} className={plan.amountMeals === 4 ? "planActive" : ""}>4</li>
                <li onClick={() => { setPlan(plans.plan6) }} className={plan.amountMeals === 6 ? "planActive" : ""}>6</li>
                <li onClick={() => { setPlan(plans.plan9) }} className={plan.amountMeals === 9 ? "planActive" : ""}>9</li>
                <li onClick={() => { setPlan(plans.plan12) }} className={plan.amountMeals === 12 ? "planActive" : ""}>12</li>
              </ul>
            </div>

            <h3>{plan.amountMeals} REFEIÇÕES</h3>
            <p>por semana</p>
            <motion.p
              variants={planPriceVariants}
              animate={planPriceVariantsControl}

              className="plan-price"
            >
              <span>R$</span> {plan.pricePerMeal.toFixed(2)} / refeição
            </motion.p>

            <Link className="plan-info-submit" to="/frozen-foods/meal-choise" >QUERO PLANO {plan.amountMeals} REFEIÇÕES</Link>
          </div>

        </article>
      </main>

      <footer className="home-footer">
        <p>Escolha um plano acima para dar uma olhada nos pratos disponíveis.</p>
      </footer>
    </div>
    </motion.div>
  );
}

export default Home;
