import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import ScrollToTop from './components/ScrollToTop';

// pages
import Home from './pages/Home';
import MealsChoice from './pages/MealsChoice';
import OrderSent from './pages/OrderSent';

const Routes = () => (
    <BrowserRouter>
        <ScrollToTop />
        <Switch>
            <Route path="/frozen-foods" exact component={Home} />
            <Route path="/frozen-foods/meal-choise" component={MealsChoice} />
            <Route path="/frozen-foods/order-sent" component={OrderSent} />
        </Switch>
    </BrowserRouter>
)

export default Routes;