import React, { useState, useContext, } from 'react';
import './MealsChoice.css';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';

import ButtonAddMeal from '../components/ButtonAddMeal';
import SelectedMeal from '../components/SelectedMeal';
import Emoji from '../components/Emoji';
import CheckoutForm from '../components/CheckoutForm';


// react-icons
import { MdClose, MdArrowBack } from 'react-icons/md';

// store
import { store } from '../store';

// dishes
import dishes from './dishes';

// about modal
import Modal from 'react-modal';
Modal.setAppElement('#root');

// modal meal
const overlayClassNameModalMeal = {
    base: "modal-overlay",
    afterOpen: "modal-overlay--after-open",
    beforeClose: "modal-overlay--before-close"
}
const classNameModalMeal = {
    base: "modal-content-meal",
    afterOpen: "modal-content--after-open",
    beforeClose: "modal-content--before-close"
}

// modal checkout
const overlayClassNameModalCheckout = {
    base: "modal-overlay",
    afterOpen: "modal-overlay--after-open",
    beforeClose: "modal-overlay--before-close"
}
const classNameModalCheckout = {
    base: "modal-content-checkout",
    afterOpen: "modal-content--after-open",
    beforeClose: "modal-content--before-close"
}

// animations
const selectedMealVariants = {
    beforeEnter: { marginLeft: "-100px", scale: 1, y: 0 },
    enter: { marginLeft: "0px", transition: { duration: 0.7 }, scale: 1, y: 0 },
}

// Inner Selected Meal component
const InnerSelectedMeal = ({ meal, onClick }) => (
    <div className="carousel-item" onClick={ onClick } >
        <img src={meal.imgImported} />
        <p>{
            (meal.description.length > 30) ? `${meal.description.substr(0,30)} ...` : meal.description
        }</p>
    </div>
)

// ===================================================== //
function MealsChoice () {
    // GLOBAL STATE
    const { plan } = useContext(store);

    // useState
    const [ modalMealState, setModalMealState] = useState({
        modalIsOpen: false,
        mealToShow: dishes[0]
    });

    const [ modalCheckoutIsOpen, setModalCheckoutIsOpen ] = useState(false);

    const [ selectedMeals, setSelectedMeals ] = useState([]);
    const [ isRemoving, setIsRemoving ] = useState(false);

    // modal meal
    const openModalMeal = (dish) => setModalMealState({ modalIsOpen: true, mealToShow: dish });
    const closeModalMeal = () => setModalMealState({ ...modalMealState, modalIsOpen: false });

    // modal checkout
    const openModalCheckout = () => setModalCheckoutIsOpen(true);
    const closeModalCheckout = () => setModalCheckoutIsOpen(false);

    const addMeal = async (meal) => {
        setIsRemoving(false);
        setSelectedMeals([ ...selectedMeals, meal ]);
    };

    const removeMeal = async (mealToRemove) => {
        const newSelectedMeals = [];

        for ( let i = 0; i < selectedMeals.length; i++) {
            let meal = selectedMeals[i];

            if( meal.img === mealToRemove.img) {
                newSelectedMeals.push(...selectedMeals.slice(i+1))
                break;
            } else {
                newSelectedMeals.push({...meal});
            }
        }

        setIsRemoving(true);
        setSelectedMeals(newSelectedMeals);
    }

    //
    const checkoutHandle = () => openModalCheckout();

    // amount meals left
    const mealsLeft = plan.amountMeals - selectedMeals.length;

    // ========================================================== //
    return (
        <motion.div
            initial={{ opacity: 0, x: 100 }}
            animate={{ opacity: 1, x: 0 }}            
        >
        <div className="meal-choice">
            
            {/* MODAL MEAL */}
            <div>
                <Modal
                    isOpen={modalMealState.modalIsOpen}
                    onRequestClose={closeModalMeal}
                    contentLabel="more info about the meal selected"
                    overlayClassName={overlayClassNameModalMeal}
                    className={classNameModalMeal}
                    closeTimeoutMS={1000}
                >
                    <MdClose className="modal-close-icon" onClick={ closeModalMeal } />
                    <article className="modal-meal-internal-content">
                        <img src={modalMealState.mealToShow.imgImported} />
                        <p>{modalMealState.mealToShow.description}</p>
                        <ButtonAddMeal onClick={() => addMeal(modalMealState.mealToShow)} className="meal-btn-add" />
                    </article>
                </Modal>
            </div>

            {/* MODAL CHECKOUT */}
            <div>
                <Modal
                    isOpen={modalCheckoutIsOpen}
                    onRequestClose={closeModalCheckout}
                    contentLabel="more info about the meal selected"
                    overlayClassName={overlayClassNameModalCheckout}
                    className={classNameModalCheckout}
                    closeTimeoutMS={1000}
                >
                    <MdClose className="modal-close-icon" onClick={ closeModalCheckout } />
                    <article className="modal-checkout-internal-content">
                        <header>
                            <Emoji symbol="😃" lable="sorriso" className="emoji-smile" />
                            <p>Obaa!!</p>
                            <p>Você escolheu {selectedMeals.length} refeições</p>
                        </header>

                        <main className="modal-checkout-internal-content-form">
                            <p>Agora, pra gente finalizar, é só escrever aqui <Emoji symbol="👇" lable="abaixo" /></p>
                            
                            <CheckoutForm selectedMeals={selectedMeals} />

                        </main>

                    </article>
                </Modal>
            </div>

            {/* HEADER */}
            <header className="meal-choise-header">
                <Link to="/frozen-foods"><MdArrowBack className="meal-choise-header-icon-back" /></Link>
                <p>escolha {plan.amountMeals} refeições</p>
            </header>

            {/* MEALS */}
            <section className="meals-wrap">
                {
                    dishes.map( (dish, i) => {
                        const img = require(`../img/${dish.img}`);
                        dish.imgImported = img;
                        return (
                            <article key={dish.img} className="meal" >
                                <img src={img} onClick={openModalMeal.bind(null, dish)} />
                                <div className="meal-info">
                                    <p className="meal-ingredients" onClick={openModalMeal.bind(null, dish)}>{
                                        (dish.description.length > 54) ? `${dish.description.substr(0,54)} ...` : dish.description
                                    }</p>
                                    <ButtonAddMeal onClick={() => addMeal(dish)} className="meal-btn-add" />
                                </div>
                            </article>
                        )
                    } )
                }
            </section>

            {/* CAROUSEL */}
            <div className="selected-meals">
                <div className="selected-meals-header">
                    <div>
                        <p className="selected-meals-left">
                            {
                                (selectedMeals.length < plan.amountMeals) ?
                                    <span className="font-red">{selectedMeals.length} </span> :
                                (selectedMeals.length > plan.amountMeals) ?
                                    <span className="font-grape">{selectedMeals.length} </span> :
                                    <span className="font-cyan">{selectedMeals.length} </span>
                            }
                            de <span className="font-cyan">{plan.amountMeals}</span> refeições
                        </p>

                        <p>{ (mealsLeft > 0) ? `adicione + ${mealsLeft} para continuar` : '' }</p>
                    </div>
                    <button onClick={ checkoutHandle } disabled={ (selectedMeals.length >= plan.amountMeals ? false : true) } className="submit-selected-meals">Pronto</button>
                </div>
                
                <motion.div
                    variants={selectedMealVariants}
                    initial="beforeEnter"
                    animate="enter"

                >
                    <div  className="carousel-wrapper" >{
                        selectedMeals.map( ( meal, i ) => {
                            const order = selectedMeals.length - i;

                            return (
                                order === 1
                                ?
                                    ( !isRemoving )
                                    ?
                                        <div style={{ order }} key={i} >
                                        <SelectedMeal enter={true} btnClose={{ onClick: removeMeal.bind(null, meal) }} >
                                            <InnerSelectedMeal onClick={ openModalMeal.bind(null, meal) } meal={meal} />
                                        </SelectedMeal>
                                        </div>
                                    :
                                        <div style={{ order }} key={i} >
                                        <motion.div
                                            variants={selectedMealVariants}
                                        >
                                            <SelectedMeal btnClose={{ onClick: removeMeal.bind(null, meal) }} >
                                                <InnerSelectedMeal onClick={ openModalMeal.bind(null, meal) } meal={meal} />
                                            </SelectedMeal>
                                        </motion.div>
                                        </div>
                                :
                                    <div style={{ order }} key={i} >
                                    <motion.div
                                        variants={selectedMealVariants}
                                    >
                                        <SelectedMeal btnClose={{ onClick: removeMeal.bind(null, meal) }} >
                                            <InnerSelectedMeal onClick={ openModalMeal.bind(null, meal) } meal={meal} />
                                        </SelectedMeal>
                                    </motion.div>
                                    </div>
                            )
                        } )
                    }</div>
                </motion.div>

            </div>

        </div>
        </motion.div>
    );
}

export default MealsChoice;