import React from 'react';

import { motion, useAnimation } from 'framer-motion';

import { MdClose } from 'react-icons/md';

const styleBtnClose = {
    position: "absolute",
    top: "-10px",
    right: "-5px",
    zIndex: 999,

    fontSize: "1.8rem",

    borderRadius: "50%",
    backgroundColor: "#fff",
    color: "#555",
    boxShadow: "0 0 3px rgba(0, 0, 0, 1)",
}

const SelectedMeal = ( { children, btnClose, enter} ) => {

    const variantsControl = useAnimation();
    const variants = {
        initial: { scale: 0, y: -100 },
        enter: { scale: 1, y: 0 },
        remove: { zoom: 0, transition: { duration: 0.3 } }
    }

    variantsControl.start("enter");

    const handleBtnClose = async () => {
        await variantsControl.start("remove");
        btnClose.onClick();
    }

    return (
       <motion.div
            initial={ enter ? "initial" : ""}
            variants={variants}
            animate={variantsControl}
            transition={{
                type: "spring",
                stiffness: 260,
                damping: 20,
                duration: 3
            }}
        >
            <div style={{ position: "relative" }}>
                <MdClose onClick={handleBtnClose} style={styleBtnClose} />
                { children }
            </div>
        </motion.div>
    );

}

export default SelectedMeal;